import React from 'react';

import './Settings.css'

import { Panel, PanelHeader, Link } from '@vkontakte/vkui/';
import { Icon24BrowserForward } from '@vkontakte/icons';

const Settings = ({id}) => (
	<Panel id={id}>
		<PanelHeader>Настройки</PanelHeader>
		<div className="settings-menu">
		<div className="settings-menu-element">
          	<div>Открыть все</div>
			<Icon24BrowserForward/>
    	</div>
			
		</div>
		<div className="settings-menu">
			<Link
        		href={`https://vk.com/my_dreams_app`}
        		target="_blank"
        		style={{ cursor: 'pointer' }}>
        		<div className="settings-menu-element">
          		<div>Сообщество с ботом</div>
				<Icon24BrowserForward/>
        		</div>
      		</Link>
			<div className="settings-menu-element">
          		<div>Подписаться на сообщество</div>
				<Icon24BrowserForward style={{margin: '0 auto'}}/>
        	</div>
			<div className="settings-menu-element">
          		<div>Настроить уведомления</div>
				<Icon24BrowserForward style={{margin: '0 auto'}}/>
        	</div>
		</div>
	</Panel>
);


export default Settings;